import { commentEnum } from "../configuration/comment-types";
export function typeReplacer(key, value) {
    // Changes "type" to be human-readable
    if (key === "type" && typeof value === "number") {
        const typeNames = [];
        const types = Object.values(commentEnum);
        types.reverse().forEach((typeValue) => {
            if (value & typeValue) {
                typeNames.push(commentEnum[typeValue]);
            }
        });
        return typeNames.join("_");
    }
    return value;
}
export function removeKeyFromObject(obj, keyToRemove) {
    if (Array.isArray(obj)) {
        return obj.map((item) => removeKeyFromObject(item, keyToRemove));
    }
    if (obj !== null && typeof obj === "object") {
        const newObj = {};
        Object.keys(obj).forEach((key) => {
            if (key !== keyToRemove) {
                newObj[key] = removeKeyFromObject(obj[key], keyToRemove);
            }
        });
        return newObj;
    }
    return obj;
}
