import { createActionsPlugin } from "@ubiquity-os/ubiquity-os-kernel";
import { run } from "./run";
import envConfigSchema from "./types/env-type";
import { pluginSettingsSchema } from "./types/plugin-input";
export default createActionsPlugin((context) => {
    return run(context);
}, {
    logLevel: process.env.LOG_LEVEL ?? "info",
    settingsSchema: pluginSettingsSchema,
    envSchema: envConfigSchema,
    ...(process.env.KERNEL_PUBLIC_KEY && { kernelPublicKey: process.env.KERNEL_PUBLIC_KEY }),
    postCommentOnError: true,
});
