import { getMinimizedCommentStatus } from "./helpers/get-comment-details";
export async function getRepo(context, params) {
    const { octokit } = context;
    return (await octokit.rest.repos.get(params)).data;
}
export async function getIssue(context, params) {
    const { octokit } = context;
    return (await octokit.rest.issues.get(params)).data;
}
export async function getPullRequest(context, pullParams) {
    const { octokit } = context;
    return (await octokit.rest.pulls.get(pullParams)).data;
}
export async function getIssueEvents(context, issueParams) {
    const { octokit } = context;
    return await octokit.paginate(octokit.rest.issues.listEvents.endpoint.merge(issueParams));
}
export async function getIssueComments(context, issueParams) {
    const { octokit } = context;
    const comments = await octokit.paginate(octokit.rest.issues.listComments.endpoint.merge(issueParams));
    await getMinimizedCommentStatus(context, comments);
    return comments;
}
export async function getPullRequestReviews(context, pullParams) {
    const { octokit } = context;
    return await octokit.paginate(octokit.rest.pulls.listReviews.endpoint.merge(pullParams));
}
export async function getPullRequestReviewComments(context, pullParams) {
    const { octokit } = context;
    return await octokit.paginate(octokit.rest.pulls.listReviewComments.endpoint.merge(pullParams));
}
export function parseGitHubUrl(url) {
    const path = new URL(url).pathname.split("/");
    if (path.length !== 5) {
        throw new Error(`[parseGitHubUrl] Invalid url: [${url}]`);
    }
    return {
        owner: path[1],
        repo: path[2],
        issue_number: Number(path[4]),
    };
}
